const htmlElement = document.documentElement;
const themeButton = document.querySelector('.switch-theme-test');
themeButton.addEventListener("click", () => changeTheme(true));

// –§—É–Ω–∫—Ü–∏—è —Å–º–µ–Ω—ã —Ç–µ–º—ã —Å–∞–π—Ç–∞ (–µ—Å–ª–∏ —Ñ—É–Ω–∫—Ü–∏—è –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è –ø–æ –∫–ª–∏–∫—É, —Ç–æ –≤ –∞—Ä–≥—É–º–µ–Ω—Ç—É —Ñ—É–Ω–∫—Ü–∏–∏ –±—É–¥–µ—Ç true)
function changeTheme(saveTheme = false) {

	// –ú–µ–Ω—è–µ–º —Ç–µ–º—É –≤ –ª—é–±–æ–º —Å–ª—É—á–∞–µ
	let currentTheme = htmlElement.classList.contains('_light') ? 'light' : 'dark';     	// –ó–∞–Ω–æ—Å–∏–º –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é —Ç–µ–∫—É—â—É—é —Ç–µ–º—É HTML
	let newTheme;                                                 												// –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è –¥–ª—è —Å–º–µ–Ω—ã –∫–ª–∞—Å—Å–∞ –≤ —Ç–µ–≥–µ html
	let newDataColorScheme;                                           										// –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è –¥–ª—è —Å–º–µ–Ω—ã –∞—Ç—Ä–∏–±—É—Ç–∞ "data-color-scheme" –≤ —Ç–µ–≥–µ html
	if (currentTheme === 'light') {
		newTheme = 'dark';
		newDataColorScheme = 'dark';
	} else {
		newTheme = 'light';
		newDataColorScheme = 'light';
	}
	htmlElement.classList.remove(`_${currentTheme}`);                         						// –£–¥–∞–ª—è–µ–º –∫–ª–∞—Å—Å —Ç–µ–∫—É—â–µ–π —Ç–µ–º—ã –¥–ª—è HTML
	htmlElement.classList.add(`_${newTheme}`);                              							// –î–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å –Ω–æ–≤–æ–π —Ç–µ–º—ã –¥–ª—è HTML
	themeButton.blur();                                             											// –£–±–∏—Ä–∞–µ–º —Ñ–æ–∫—É—Å —Å –∫–Ω–æ–ø–∫–∏
	htmlElement.setAttribute("data-color-scheme", newDataColorScheme);              			// –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∞—Ç—Ä–∏–±—É—Ç "data-color-scheme" –¥–ª—è HTML

	// –ï—Å–ª–∏ –∞—Ä–≥—É–º–µ–Ω—Ç —Ñ—É–Ω–∫—Ü–∏–∏ saveTheme = true (—Ç.–µ. —Ñ—É–Ω–∫—Ü–∏—è –∑–∞–ø—É—â–µ–Ω–∞ –ø–æ –∫–ª–∏–∫—É), —Ç–æ –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –≤ –±—Ä–∞—É–∑–µ—Ä –≤ localStorage –∑–Ω–∞—á–µ–Ω–∏–µ —Ç–µ–º—ã –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è 
	saveTheme ? localStorage.setItem('theme', newTheme) : null;
}

// –û—Å–Ω–æ–≤–Ω–æ–π –∫–æ–¥ (–°–∞–º–æ–≤—ã–∑—ã–≤–∞–µ–º–∞—è —Ñ—É–Ω–∫—Ü–∏—è)
(function () {
	// –ü–æ–ª—É—á–∞–µ–º —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—É—é —Ç–µ–º—É –∏–∑ localStorage (theme - —ç—Ç–æ –∫–ª—é—á –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è —Ç–µ–º—ã, –≤—ã–±—Ä–∞–Ω–Ω–æ–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º)
	// –°–≤–æ–π—Å—Ç–≤–æ localStorage –ø–æ–∑–≤–æ–ª—è–µ—Ç –ø–æ–ª—É—á–∏—Ç—å –¥–æ—Å—Ç—É–ø –∫ Storage –æ–±—ä–µ–∫—Ç—É. localStorage –∞–Ω–∞–ª–æ–≥–∏—á–Ω–æ —Å–≤–æ–π—Å—Ç–≤—É sessionStorage. –†–∞–∑–Ω–∏—Ü–∞ —Ç–æ–ª—å–∫–æ –≤ —Ç–æ–º, —á—Ç–æ —Å–≤–æ–π—Å—Ç–≤–æ sessionStorage —Ö—Ä–∞–Ω–∏—Ç –¥–∞–Ω–Ω—ã–µ –≤ —Ç–µ—á–µ–Ω–∏–µ —Å–µ–∞–Ω—Å–∞ (–¥–æ –∑–∞–∫—Ä—ã—Ç–∏—è –±—Ä–∞—É–∑–µ—Ä–∞), –≤ –æ—Ç–ª–∏—á–∏–µ –æ—Ç –¥–∞–Ω–Ω—ã—Ö, –Ω–∞—Ö–æ–¥—è—â–∏—Ö—Å—è –≤ —Å–≤–æ–π—Å—Ç–≤–µ localStorage, –∫–æ—Ç–æ—Ä—ã–µ –Ω–µ –∏–º–µ—é—Ç –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–π –ø–æ –≤—Ä–µ–º–µ–Ω–∏ —Ö—Ä–∞–Ω–µ–Ω–∏—è –∏ –º–æ–≥—É—Ç –±—ã—Ç—å —É–¥–∞–ª–µ–Ω—ã —Ç–æ–ª—å–∫–æ —Å –ø–æ–º–æ—â—å—é JavaScript.
	// –ü—Ä–∏ –ø–µ—Ä–≤–æ–º –∑–∞—Ö–æ–¥–µ –Ω–∞ —Å–∞–π—Ç —ç—Ç–∞ –∫–æ–Ω—Å—Ç–∞–Ω—Ç–∞ –±—É–¥–µ—Ç –ø—É—Å—Ç–æ–π. –û–Ω–∞ –∑–∞–ø–æ–ª–Ω—è–µ—Ç—Å—è –≤  —Ñ—É–Ω–∫—Ü–∏–∏ —Å–º–µ–Ω—ã —Ç–µ–º—ã changeTheme
	const saveUserTheme = localStorage.getItem('theme');

	// –†–∞–±–æ—Ç–∞ —Å —Å–∏—Å—Ç–µ–º–Ω—ã–º–∏ –Ω–∞—Å—Ç—Ä–æ–π–∫–∞–º–∏
	let userTheme;
	if (window.matchMedia) {
		// –ó–∞–Ω–æ—Å–∏–º –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é userTheme —Ü–≤–µ—Ç–æ–≤—É—é —Ç–µ–º—É –°–∏—Å—Ç–µ–º—ã –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
		userTheme = window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';
	}
	// –°—Ç–∞–≤–∏–º –ø—Ä–æ—Å–ª—É—à–∫—É –Ω–∞ –°–æ–±—ã—Ç–∏–µ –∏–∑–º–µ–Ω–µ–Ω–∏—è —Ç–µ–º—ã (—Å–ø—Ä–æ—Å–ª—É—à–∫—É —Å—Ç–∞–≤–∏–º –Ω–∞ —Ç–µ–º–Ω—É—é —Ç–µ–º—É)
	window.matchMedia('(prefers-color-scheme: dark)').addEventListener("change", e => {
		// –ï—Å–ª–∏ –≤—ã–±—Ä–∞–Ω–Ω–∞—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º —Ç–µ–º–∞ –æ—Ç–ª–∏—á–∞–µ—Ç—Å—è –æ—Ç —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω–æ–π —Ä–∞–Ω–µ–µ —Ç–µ–º—ã (if (window.matchMedia('(prefers-color-scheme: dark)').matches != saveUserTheme), —Ç–æ –∑–∞–ø—É—Å–∫–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é –∏–∑–º–µ–Ω–µ–Ω–∏—è —Ç–µ–º—ã changeTheme(), –≤ –ø—Ä–æ—Ç–∏–≤–Ω–æ–º —Å–ª—É—á–∞–µ –Ω–∏—á–µ–≥–æ –Ω–µ –¥—É–ª–∞–µ–º (null)
		// –§—É–Ω–∫—Ü–∏—è changeTheme() –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è –ø—Ä–∏ —Å–º–µ–Ω–µ –°–∏—Å—Ç–µ–º–Ω—ã—Ö –Ω–∞—Å—Ç—Ä–æ–µ–∫ —Ç–µ–º—ã, –ø—Ä–∏ —É—Å–ª–æ–≤–∏–∏, —á—Ç–æ –Ω–µ—Ç —Å–æ—Ö–æ—Ä–∞–Ω–µ–Ω–Ω—ã—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º –Ω–∞—Å—Ç—Ä–æ–µ–∫ —Ç–µ–º—ã –Ω–∞ —Å–∞–π—Ç–µ
		!saveUserTheme ? changeTheme() : null;
	});

	// –§—É–Ω–∫—Ü–∏—è —É—Å—Ç–∞–Ω–æ–≤–∫–∏ —Ç–µ–º—ã –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫e —Å—Ç—Ä–∞–Ω–∏—Ü—ã (–°–∞–º–æ–≤—ã–∑—ã–≤–∞–µ–º–∞—è —Ñ—É–Ω–∫—Ü–∏—è)
	function setThemeClass() {
		if (saveUserTheme) {                                      													// –ï—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —Ä–∞–Ω–µ–µ –≤—ã–±—Ä–∞–ª —Ç–µ–º—É —Å–∞–π—Ç–∞ (–ø–µ—Ä–µ–º–µ–Ω–Ω–∞—è saveUserTheme - –Ω–µ –ø—É—Å—Ç–∞—è)
			htmlElement.classList.add(`_${saveUserTheme}`);                 									// –î–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å —ç—Ç–æ–π —Ç–µ–º—ã –¥–ª—è HTML
			htmlElement.setAttribute("data-color-scheme", saveUserTheme);       							// –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∞—Ç—Ä–∏–±—É—Ç "data-color-scheme" –¥–ª—è HTML
		} else {                                              															// –ò–Ω–∞—á–µ
			htmlElement.classList.add(`_${userTheme}`);                   										// –î–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å –°–∏—Å—Ç–µ–º–Ω–æ–π —Ç–µ–º—ã –¥–ª—è HTML
			htmlElement.setAttribute("data-color-scheme", userTheme);         								// –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∞—Ç—Ä–∏–±—É—Ç "data-color-scheme" –¥–ª—è HTML –∏–∑ —Å–∏—Å—Ç–µ–º—ã –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
		}
	}
	setThemeClass();

}());
